@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{background-image:radial-gradient(circle, #1f3756, #1b2e4f, #182648, #161d41, #141539);background-repeat:no-repeat;width:100%;height:100vh;font-family:'Barlow Semi Condensed', sans-serif}.container{padding:2rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-around;justify-content:space-around}.overlay{width:100%;height:100vh;position:absolute;left:0;top:0;background-color:rgba(0,0,0,0.5)}.rules-modal{position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around;z-index:99;background-color:#fff;width:500px;height:60vh;border-radius:20px}.rules-modal .modal-header{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;width:80%}.rules-modal .modal-header .modal-title{color:#3b4363;font-size:2rem}.rules-modal .modal-header .close{width:20px;cursor:pointer}.rules-modal img{width:80%;padding:1rem 0}.score-board{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;width:700px;border:2px solid #606e85;border-radius:10px;padding:0.8rem 1.5rem;margin:0 auto}.score-board .score{text-align:center;background-color:#fff;padding:0.8rem 3rem;border-radius:1rem}.score-board .score .score-text{color:#2a46c0;font-weight:300}.score-board .score .score-number{color:#3b4363;font-size:3.5rem;font-weight:700}.dice{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;width:200px;height:200px;border-radius:50%;box-shadow:inset 0px -9px 2px 0px rgba(0,0,0,0.36);cursor:pointer;transition:0.3s ease-in-out}.dice:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.dice .inner-circle{width:160px;height:160px;background-color:white;border-radius:50%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;box-shadow:inset 0px 10px 2px 0px rgba(0,0,0,0.2)}.dice .inner-circle .icon{width:80px}.dice-controller{position:absolute;top:55%;left:50%;-webkit-transform:translate(-50%, -10%);transform:translate(-50%, -10%);display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.dice-controller .dice{position:absolute}.dice-controller .paper{left:-20%;top:-30%}.dice-controller .scissor{right:-20%;top:-30%}.dice-controller .rock{bottom:-10%;right:50%;-webkit-transform:translateX(50%);transform:translateX(50%)}.dice-field{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around;width:50%;height:70vh;margin:0 auto}.dice-field .diced{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.dice-field .diced .blank{background-image:transparent;box-shadow:none}.dice-field .diced .blank .inner-circle{background-color:#19233a}.dice-field .diced span{padding:2rem 0;font-size:1.4rem;letter-spacing:0.5px;color:#fff;white-space:nowrap}.dice-field .diced .dice{pointer-events:none}.dice-field .game-result{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin:0 1.5rem}.dice-field .game-result h2{padding:0.5rem 0;font-size:3.5rem;color:#fff;white-space:nowrap}.dice-field .game-result .play-again{font-size:1rem;color:#b62a2a;font-weight:500;padding:1rem 0;border-radius:10px;border:none;cursor:pointer}.show-rules{position:absolute;right:20%;bottom:8%;color:#fff;border:2px solid #fff;padding:0.7rem 2rem;border-radius:10px;cursor:pointer}@media screen and (max-width: 768px){.score-board{width:100%}.score-board .score{padding:0.2rem 2rem}.score-board .game-title img{width:65%}.dice{width:150px;height:150px}.dice .inner-circle{width:120px;height:120px}.dice .inner-circle .icon{width:50px}.dice-controller{top:45%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.dice-controller .controller{width:90%;min-width:250px}.dice-controller .rock{bottom:-20%}.dice-controller .paper{left:-15%}.dice-controller .scissor{right:-15%}.show-rules{right:10%}.dice-field{width:100%;height:55vh}.dice-field .diced{width:200px;-webkit-flex-direction:column-reverse;flex-direction:column-reverse}.dice-field .game-result{position:absolute;bottom:15%;left:50%;-webkit-transform:translate(-50%, 15%);transform:translate(-50%, 15%);margin:0}.dice-field .game-result h2{font-size:2rem}.dice-field .game-result .play-again{font-size:0.8rem}}@media screen and (max-width: 450px){.rules-modal{width:100%;height:100%;border-radius:0;-webkit-justify-content:space-evenly;justify-content:space-evenly;left:0;top:0;-webkit-transform:translate(0%, 0%);transform:translate(0%, 0%)}.rules-modal img{width:90%}.show-rules{bottom:2%}}

